import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ObservableService } from '../services/observable.service';
import { member } from '../models/member';

@Component({
  selector: 'allcelebrities',
  templateUrl: './allcelebrities.component.html',
  styleUrls: ['./allcelebrities.component.css']
})
export class AllcelebritiesComponent implements OnInit {

    // imageUrl: any = "http://13.58.150.195:4300/";
    //demo
     imageUrl: any = "http://18.219.37.107:4300/";
    //imageUrl: any = "http://192.168.2.134:4300/";

    //alpha url:
    //imageUrl : any = "http://prodapi.celebkonect.com:4300/";
  private allCelebs: Array<any>
  private cbProfile: boolean;
  private cbprofmask: boolean;
  celebrity: member;
  id : any;
  email : any;
  memPreferences : Array<any>;

  constructor(
    private routSvc: Router,
    private cbOvc: ObservableService,
    public route: ActivatedRoute
  ) {
    this.cbProfile = false;
    this.cbprofmask = false;
  }

  ngOnInit() {
    this.getAllCBKCelebrities();
    this.email = localStorage.getItem('loginSessId');
    // console.log("seesion" + localStorage.getItem('loginSessId'));
    this.id = localStorage.getItem('memberId');

    this.getMemberByEmail(this.email);
   // this.ongetFansandFolowers(this.id);
  }

  getAllCBKCelebrities() {
    this.cbOvc.getAllCelebrities().subscribe(data => {
      this.allCelebs = data;
      this.allCelebs = this.allCelebs.filter(m => m.IsDeleted == false);
      // console.log(data);
    });
  }

  openCbProfile(id) {
    //alert(id);
    this.cbOvc.getMemberByID(id).subscribe(data => {
      this.celebrity = data;
      
    });  
    this.cbProfile = true;
    this.cbprofmask = true;
    return this.celebrity;
  }

  getMemberByEmail(email: any) {
    email = this.email;
    this.cbOvc.getMemberByEmail(this.email).subscribe(x => {
      // console.log("testingid"+ x._id);
      this.id = x._id;
     // this.test = x._id;
      localStorage.setItem('memberId', x._id);
      // console.log("final" + this.id);
      
      this.ongetFansandFolowers(this.id);
    });
    return this.id;
  }

  closeCbProfile() {
    this.cbProfile = false;
    this.cbprofmask = false;
  }

  ongetFansandFolowers(id){

    this.cbOvc.getFansandFolowers(id).subscribe(data => {
     this.memPreferences = data;
     
   });
 }

}



