@HostListener("window:scroll", [])
onWindowScroll(){
  let hometab = this.document.documentElement.scrollTop || this.document.body.scrollTop || 0;

  let hmtabid = document.getElementById("hm").offsetTop-0;
  let abttab = document.getElementById("abt").offsetTop-80;
  let cnttab = document.getElementById("cnt").offsetTop-80;
  let feutab = document.getElementById("fet").offsetTop-80;

  let hdr1tab = document.getElementById("hmrt");
  let hdr2tab = document.getElementById("abrt");
  let hdr3tab = document.getElementById("cnrt");
  let hdr4tab = document.getElementById("feut");

  if(hometab >= hmtabid){
    hdr1tab.classList.add("list_active");
    hdr2tab.classList.remove("list_active");
    //hdr3tab.classList.remove("list_active");
    hdr4tab.classList.remove("list_active");
  }
  if(hometab > abttab){
    hdr1tab.classList.remove("list_active");
    hdr2tab.classList.add("list_active");
    //hdr3tab.classList.remove("list_active");
    hdr4tab.classList.remove("list_active");
  }
  // if(hometab > cnttab){
  //   hdr1tab.classList.remove("list_active");
  //   hdr2tab.classList.remove("list_active");
  //   hdr3tab.classList.add("list_active");
  //   hdr4tab.classList.remove("list_active");
  // }
  if(hometab > feutab){
    hdr1tab.classList.remove("list_active");
    hdr2tab.classList.remove("list_active");
    //hdr3tab.classList.remove("list_active");
    hdr4tab.classList.add("list_active");
  }
}



