import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';


import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { HomeComponent } from './home/home.component';

import { ProfileComponent } from './profile/profile.component';
import { MenuService } from './services/menu.service';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { FooterComponent } from './footer/footer.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { FaqsComponent } from './faqs/faqs.component';
import { ContactusComponent } from './contactus/contactus.component';
import { TermsComponent } from './terms/terms.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { CommunityComponent } from './community/community.component';
import { HelpComponent } from './help/help.component';
import { CelebritiesComponent } from './celebrities/celebrities.component';
import { CreditsComponent } from './credits/credits.component';
import { ObservableService } from './services/observable.service';
import { MainpageComponent } from './mainpage/mainpage.component';
import { TransactionsComponent } from './transactions/transactions.component';
import { RegisterService } from './services/register.service';
import { AllcelebritiesComponent } from './allcelebrities/allcelebrities.component';
//import { AuthGuard } from './guards/auth.guard';
//import { NotAuthGuard } from './guards/notAuth.guard';
import { AuthGuard } from './guards/auth.guard';
import { NotAuthGuard } from './guards/notAuth.guard';
import { NgxCarouselModule } from 'ngx-carousel';
import 'hammerjs';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Ng2CarouselamosModule } from 'ng2-carouselamos';
import {Ng2PageScrollModule} from 'ng2-page-scroll';
import { MypreferencesComponent } from './mypreferences/mypreferences.component';
import { EqualValidator } from './home/password.match.directive';
import { NgxPasswordToggleModule } from 'ngx-password-toggle';

//import { Ng2ScrollableModule } from 'ng2-scrollable';



@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,

    ProfileComponent,
    HomeComponent,
    RegisterComponent,
    LoginComponent,
    FooterComponent,
    AboutusComponent,
    FaqsComponent,
    ContactusComponent,
    TermsComponent,
    PrivacyComponent,
    CommunityComponent,
    HelpComponent,
    CelebritiesComponent,
    CreditsComponent,
    MainpageComponent,
    TransactionsComponent,
    AllcelebritiesComponent,
    MypreferencesComponent,
    EqualValidator
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    NgxCarouselModule,
    Ng2CarouselamosModule,
    BrowserAnimationsModule,
    NgxPasswordToggleModule,
    Ng2PageScrollModule,
    //Ng2ScrollableModule,
    RouterModule.forRoot([
      { path: '', redirectTo: 'home', pathMatch: 'full', },
      { path: 'home', component: HomeComponent},
      //{ path : 'register', component : RegisterComponent},
      //{ path : 'login', component : LoginComponent},
      { path: 'profile', component: ProfileComponent, canActivate: [AuthGuard]},
      { path: 'aboutus', component: AboutusComponent, canActivate: [AuthGuard]},
      { path: 'faqs', component: FaqsComponent},
      { path: 'contactus', component: ContactusComponent, canActivate: [AuthGuard]},
      { path: 'termsandconditions', component: TermsComponent},
      { path: 'privacypolicy', component: PrivacyComponent},
      { path: 'help', component: HelpComponent, canActivate: [AuthGuard]},
      { path: 'communityguidlines', component: CommunityComponent},
      { path: 'mainpage', component: MainpageComponent, canActivate: [AuthGuard]},
      { path: 'celebrities', component: CelebritiesComponent, canActivate: [AuthGuard]},
      { path: 'transactions', component: TransactionsComponent, canActivate: [AuthGuard] },
      { path: 'allcelebrities', component: AllcelebritiesComponent, canActivate: [AuthGuard] },
      { path: 'credits', component: CreditsComponent, canActivate: [AuthGuard] },
      { path : 'mypreferences', component : MypreferencesComponent, canActivate: [AuthGuard]}
    ])
  ],
  providers: [
    MenuService,
    ObservableService,
    RegisterService,
    AuthGuard, NotAuthGuard
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



