import { Component, Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Members } from '../models/memberregistration';
import { Preferences } from '../models/preferences';
//import { Recommendations } from '../models/recommendations';
//import { SurveyQuestions } from '../models/surveyquestions';

@Injectable()

export class RegisterService {
  //http: Http;
    //baseUrl = "http://13.58.150.195:4300/";
    //demo
    baseUrl = "http://18.219.37.107:4300/";
    //local
    // baseUrl = "http://192.168.2.134:4300/";

  //alpha url:
  //baseUrl = "http://prodapi.celebkonect.com:4300/";

  constructor(private http: Http) {

  }


  onRegisterMember(member: any) {
    console.log(member);
    let body: any = {};
    body.name = member.firstname;
    body.username = member.firstname + member.lastname;
    body.mobileNumber = member.mobileNumber;
    body.location = member.location;
    body.profilePic = "";
    body.dateOfBirth = member.dateOfBirth;
    body.address = member.location;
    body.availableCredits = "0".toString();
    body.profession = member.profession;
    body.password = member.password;
    body.confirmPassword = member.confirmPassword;
    body.email = member.email;
    body.gender = member.gender;
    body.loginType = "Web Portal";
    body.country = member.country;
    body.role = "member";
    body.referralCode = member.referralCode.toString();
    console.log(body);
    return this.http.post(this.baseUrl + 'users/memberRegistrations', body).map((res: Response) =>
      res.json());
  }

  onComLog(member: any) {

    let comlog: any = {};
    comlog.mode_ids = "email";
    comlog.from_addr = "";
    comlog.to_addr = member.email;
    comlog.content = "";

    return this.http.post(this.baseUrl + 'comLog/createComLog', comlog).map((res: Response) =>
      res.json());
  }

  onCreateInfo(member: any) {
    let create: any = {};
    create.email = member.email;
    create.username = member.firstname + member.lastname;
    create.password = member.password;
    create.mobileNumber = member.mobileNumber;
    return this.http.post(this.baseUrl + 'logininfo/create', create).map((res: Response) =>
      res.json());
  }

  onGetMemberByEmail(email: any){
    console.log("Service:"+ email);
    return this.http.get(this.baseUrl + 'users/getMemberByEmail/' + email + '').map((res: Response) => res.json());

  }
  
  onGetPreferencesList(): Observable<Preferences[]> {
    return this.http.get(this.baseUrl + 'preferences/getPreferencesList').map((res: Response) =>
      res.json());
  }

  onAddPreferences(userId: any, preferences: any) {
    console.log(userId);
    console.log(preferences);
    let body: any = {};
    body.userId = userId;
    body.preferences =preferences;
    console.log(body);
    return this.http.post(this.baseUrl + 'memberpreferences/setMemberPreferences', body).map((res: Response) =>
      res.json());
  }

  onGetCelebritiesByPreferences(userid:any): Observable<Members[]> {
    return this.http.get(this.baseUrl + 'memberpreferences/getCelebritiesBySelectedPreferences/' + userid + '').map((res: Response) =>
      res.json());
  }

  onCreateMemberChoice(memberChoice,formData: any) {
    formData.append('memberId', memberChoice.memberId);
    formData.append('celebrityName', memberChoice.celebrityName);
    formData.append('industry', memberChoice.industry);
    formData.append('createdBy', memberChoice.createdBy);
    return this.http.post(this.baseUrl + 'memberchoice/createMemberChoice', formData).map((res: Response) => res.json());
  }

  onupload(formData: any) {

    return this.http.post(this.baseUrl + 'memberchoice/createMemberChoice', formData).map((res: Response) => res.json());
  }

  onGetSurveyForMember(userid: any) {
    return this.http.get(this.baseUrl + 'celebSurvey/getSurveyForMember/' + userid + '').map((res: Response) =>
      res.json());
  }

  onCreateSurveySubmissions(surveys: any) {
    
    let body: any = {};
    body.memberId = surveys.memberId;
    body.celebSurveyId = surveys.celebSurveyId;
    body.qids = [surveys.qids];
    body.questions = [surveys.questions];
    body.answers = [surveys.answer];
    body.createdBy = surveys.createdBy;
    console.log(body);
    return this.http.post(this.baseUrl + 'celebSurveySubmissions/create', body).map((res: Response) =>
      res.json());
  }
    
  onGetMemberById(user_id: any) {
    //let headers = new Headers();
    //headers.append('Content-Type', 'application/json');
    //let params = new URLSearchParams();
    //params.append("user_id", user_id)

    //let params = new HttpParams().set("user_id", user_id);

    return this.http.get(this.baseUrl + 'users/getMember/' + user_id + '').map((res: Response) =>
      res.json());
    // }
  }

}



