import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { DOCUMENT } from "@angular/platform-browser";
import { MenuService } from '../services/menu.service';
import { Router, ActivatedRoute } from '@angular/router';

// import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Directive, forwardRef, Attribute,OnChanges, SimpleChanges,Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl, FormArray , NG_VALIDATORS,Validator, AbstractControl, ValidatorFn} from '@angular/forms';
import { ObservableService } from '../services/observable.service';
import { AuthGuard } from '../guards/auth.guard';

@Component({
  selector: 'footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  formContactKonect: FormGroup;
  message;
  messageClass;
  user: any;
  previousUrl;
  processing = false;

  public kont_msk : boolean;
  public kont_pop : boolean;

  recogList: Array<any>
  isSubmitted: boolean = false;

  public show_msg : boolean;

  public show_Termspage : boolean;
  public show_termsmask : boolean;

  public show_faqspage : boolean;
  public show_faqsmask : boolean;

  public show_Privacypage : boolean;
  public show_Privacymask : boolean;
  
  
  @Inject(DOCUMENT) private document: Document
  constructor(
    private menuSvc : MenuService,
     private routeSvc : Router,
     private Obsvc : ObservableService,
     private frmBuilder : FormBuilder,
     public route: ActivatedRoute,
     private authGuard: AuthGuard,
  ) { 
    this.recogList = this.Obsvc.getRecoglist();
    this.kont_msk = false;
    this.kont_pop = false;
    this.show_msg = false;

    this.show_Termspage = false;
    this.show_termsmask = false;

    this.show_faqspage = false;
    this.show_faqsmask = false;

    this.show_Privacypage = false;
    this.show_Privacymask = false;
     
  }

  showTermspage(){
    this.show_Termspage = true;
    this.show_termsmask = true;
    
  }
  closeTermspage(){
    this.show_Termspage = false;
    this.show_termsmask = false;
  }



  showFaqspage(){
    this.show_faqspage = true;
    this.show_faqsmask = true;
  }
  closeFaqspage(){
    this.show_faqspage = false;
    this.show_faqsmask = false;
  }

  showPrivacypage(){
    this.show_Privacypage = true;
    this.show_Privacymask = true;
  }
  closePrivacypage(){
    this.show_Privacypage = false;
    this.show_Privacymask = false;
  }





  openKontForm(){
    this.kont_msk = true;
    this.kont_pop = true;
    
  }
  closeKontForm(){
    this.kont_msk = false;
    this.kont_pop = false;
    
  }

  onContactKonectFormGroup() {
    this.formContactKonect = this.frmBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      mobile: ['', Validators.required],
      subject: ['', Validators.required],
      message: ['', Validators.required],
      howDoYouHearAboutUs : ['', Validators.required]
    });
  }


  onSendContactUs() {
    const contactUs = {
      name: this.formContactKonect.get('name').value,
      email: this.formContactKonect.get('email').value,
      mobile: this.formContactKonect.get('mobile').value,
      subject: this.formContactKonect.get('subject').value,
      message: this.formContactKonect.get('message').value,
      howDoYouHearAboutUs : this.formContactKonect.get('howDoYouHearAboutUs').value
    }

    console.log(contactUs);
    this.isSubmitted = true;
    if(this.formContactKonect.valid){
      this.Obsvc.onSendContactUs(contactUs).subscribe(data => {
        console.log(data);
        if(data.message == "Email submitted successfully!!"){
          // this.kont_msk = false;
          // this.kont_pop = false;
          setTimeout(() => {
            this.kont_msk = false;
            this.kont_pop = false;
            this.show_msg = false;
          },2000);
          this.message = data.message;
          this.show_msg = true;
          this.messageClass = "alert alert-success";
          this.formContactKonect.reset();
          
        }
        else{
          // this.kont_msk = false;
          // this.kont_pop = false;
        }
      });
      this.isSubmitted = false;
    }
 
  }

  ngOnInit() {
    this.onContactKonectFormGroup();
  }

}



