import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { DOCUMENT } from "@angular/platform-browser";
import { MenuService } from '../services/menu.service';
import { Router, ActivatedRoute } from '@angular/router';

// import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Directive, forwardRef, Attribute,OnChanges, SimpleChanges,Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl, FormArray , NG_VALIDATORS,Validator, AbstractControl, ValidatorFn} from '@angular/forms';
import { ObservableService } from '../services/observable.service';
import { AuthGuard } from '../guards/auth.guard';
//import { scrollTo } from 'ng2-utils';

@Component({
  selector: 'Header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  private logoBg : String;
  private showPopup : boolean;
  private showMask : boolean;
  private showMyMenu : boolean;
  private showMyMenu2 : boolean;
  private regPopup : boolean;
  private showMask2 : boolean;
  private tab1 : boolean;
  private tab2 : boolean;
  private tab3 : boolean;
  private tab4: boolean;
  public show_lgmsg : boolean;

  loginForm: FormGroup;
  formContactKonect: FormGroup;
  message;
  messageClass;
  user: any;
  previousUrl;
  processing = false;

  private navIsFixed : boolean;
  private logoFixed : boolean;
  private loginFixed : boolean;
  private menuFixed : boolean;

  menuList : Array<any>;
  private email : any;
  private id : any;
  private memberId: any;
  userbyemail: any;
  private sub: any;
  public rspmask : boolean;

  public kont_msk : boolean;
  public kont_pop : boolean;

  recogList: Array<any>
  isSubmitted: boolean = false;

  public show_msg : boolean;

  constructor(
     private menuSvc : MenuService,
     private routeSvc : Router,
     private Obsvc : ObservableService,
     private frmBuilder : FormBuilder,
     public route: ActivatedRoute,
     private authGuard: AuthGuard,

     @Inject(DOCUMENT) private document: Document
    ) {
      this.recogList = this.Obsvc.getRecoglist();
    this.logoBg = "assets/images/logo-trans2.png";
    this.menuList = this.menuSvc.getMenuList();
    this.showPopup = false;
    this.showMask = false;
    this.creatLoginForm();
    this.user = {};
    this.regPopup = false;
    this.showMask2 = false;

    this.tab1 = true;
    this.tab2 = false;
    this.tab3 = false;
    this.tab4 = false;
    

    this.navIsFixed = false;
    this.logoFixed = false;
    this.loginFixed = false;
    this.menuFixed = false;

    //this.showMyMenu2 = false;
    //this.showMyMenu = true;
      this.rspmask = false;

    //this.email = localStorage.getItem('loginSessId');
    this.kont_msk = false;
    this.kont_pop = false;

     this.show_msg = false;
     this.show_lgmsg = false;

  }

  openKontForm(){
    this.kont_msk = true;
    this.kont_pop = true;
    
  }
  closeKontForm(){
    this.kont_msk = false;
    this.kont_pop = false;
    
  }

  slideMyMenu(){
    let mymenuid =  this.document.getElementById("loginId");
    mymenuid.classList.add("cbk_zero");
    this.rspmask = true;
  }
  clSlideMenu(){
   
    let mymenuid =  this.document.getElementById("loginId");
    mymenuid.classList.remove("cbk_zero");
    this.rspmask = false;
  }

  creatLoginForm(){
    this.loginForm = this.frmBuilder.group({
      email : ['', Validators.required],
      password : ['', Validators.required]
    });
  }

  onContactKonectFormGroup() {
    this.formContactKonect = this.frmBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      mobile: ['', Validators.required],
      subject: ['', Validators.required],
      message: ['', Validators.required],
      howDoYouHearAboutUs : ['', Validators.required]
    });
  }


  disableForm(){
    this.loginForm.controls['email'].disable();
    this.loginForm.controls['password'].disable();
  }

  enableForm(){
    this.loginForm.controls['email'].enable();
    this.loginForm.controls['password'].enable();
  }

  onLoginSubmit() {

    const user = {
      email : this.loginForm.get('email').value,
      password : this.loginForm.get('password').value
    }
    this.isSubmitted=true;
   
    this.Obsvc.login(user).subscribe(data =>{
    // console.log(data);
      this.email=user.email;
      
     //this.id = id;
      if(data.message == "login sucessfully"){
        //this.message = data.message;
        //this.messageClass = "alert alert-success";
        this.processing = true;
        
        //this.disableForm();
        this.loginForm.reset();
        this.isSubmitted=false;
               
        localStorage.setItem('loginSessId', user.email);
        this.Obsvc.storeUserData(data.token, data.user);

         this.routeSvc.navigate(['/profile/'])
        this.showPopup = false;
        this.showMask = false;
        this.rspmask = false;
        //this.showMyMenu2 = true;
        //this.showMyMenu = false;
        
      } else{
        //this.message = data.message;
        this.processing = false;
        //this.enableForm();
        //this.messageClass = "alert alert-danger";
        this.show_lgmsg = true;
        //this.showMyMenu2 = true;
       // this.showMyMenu = false;
       localStorage.setItem('loginSessId', user.email);
        
      }
    });

  }

  onLogout() : void{
    
    this.Obsvc.logout();
      this.routeSvc.navigate(['/']);
      this.rspmask = false;
      
  
  }
  onGetMemberByEmail() {
    const user = {
      email: this.loginForm.get('email').value,
      password: this.loginForm.get('password').value
    }
    this.Obsvc.getMemberByEmail(user.email).subscribe(result => {
      this.userbyemail = result;
      console.log("data:" + this.userbyemail);
      //this.email = this.userbyemail.email;
      this.memberId = this.userbyemail._id;
    });
    return this.userbyemail;
  }

 

  ShowMyPopup(){
    this.showPopup = true;
    this.showMask = true;
    this.show_lgmsg=false;
    this.isSubmitted=false;
  }

  closePopup(){
    this.showPopup = false;
    this.showMask = false;
  }

  
  showRegPopup(){
    this.regPopup = true;
    this.showMask2 = true;
    this.showPopup = false;
    this.showMask = false;
  }

  closeRegiPop(){
    this.regPopup = false;
    this.showMask2 = false;
  }

  ngOnInit() {
   this.onGetMemberByEmail();

   this.route.params.subscribe(params => {
    this.id =params['id'];
    console.log("id:"+this.id);
   });

   if (this.authGuard.redirectUrl) {
     this.messageClass = 'alert alert-danger'; 
     this.message = 'You must be logged in to view that page.'; 
     this.previousUrl = this.authGuard.redirectUrl;
     this.authGuard.redirectUrl = undefined; 
   }
   this.onContactKonectFormGroup();
  }

  onSendContactUs() {
    const contactUs = {
      name: this.formContactKonect.get('name').value,
      email: this.formContactKonect.get('email').value,
      mobile: this.formContactKonect.get('mobile').value,
      subject: this.formContactKonect.get('subject').value,
      message: this.formContactKonect.get('message').value,
      howDoYouHearAboutUs : this.formContactKonect.get('howDoYouHearAboutUs').value
    }

    console.log(contactUs);
    this.isSubmitted = true;
    if(this.formContactKonect.valid){
      this.Obsvc.onSendContactUs(contactUs).subscribe(data => {
        console.log(data);
        if(data.message == "Email submitted successfully!!"){
          // this.kont_msk = false;
          // this.kont_pop = false;
          setTimeout(() => {
            this.kont_msk = false;
            this.kont_pop = false;
            this.show_msg = false;
          },2000);
          this.message = data.message;
          this.show_msg = true;
          this.messageClass = "alert alert-success";
          this.formContactKonect.reset();
          
        }
        else{
          // this.kont_msk = false;
          // this.kont_pop = false;
        }
      });
      this.isSubmitted = false;
    }
 
  }

  getRegiTabs(id : string, event){
    switch(id){
      case 'prof' :
      this.tab1 = true;
      this.tab2 = false;
      this.tab3 = false;
      this.tab4 = false;
      event.target.classList.add('reg_active');
      break;

      case 'inter' :
      this.tab2 = true;
      this.tab1 = false;
      this.tab3 = false;
      this.tab4 = false;
      event.target.classList.add('reg_active');
      break;

      case 'pref' :
      this.tab2 = false;
      this.tab1 = false;
      this.tab3 = true;
      this.tab4 = false;
      event.target.classList.add('reg_active');
      break;

      case 'fin' :
      this.tab2 = false;
      this.tab1 = false;
      this.tab3 = false;
      this.tab4 = true;
      event.target.classList.add('reg_active');
      break;

      case 'defualt' :
      this.tab1 = true;
      break;
    }  
}

@HostListener("window:scroll", [])
onWindowScroll() {
  let number = this.document.documentElement.scrollTop || this.document.body.scrollTop || 0;
  if(number > 10){
    this.navIsFixed = true;
    this.logoFixed = true;
    this.loginFixed = true;
    this.menuFixed = true;
  }
  else if(this.navIsFixed && number < 10){
    this.navIsFixed = false;
    this.logoFixed = false;
    this.loginFixed = false;
    this.menuFixed = false;
  }
}


}



